.steps {
    width: 100%;
     margin-bottom: 120px;
     @media (max-width: $md-4) {
        margin-bottom: 60px;
     }
    // .steps__container

    &__container {
    }

    // .steps__body

    &__body {
        width: 100%;
        height: 100%;
    }

    // .steps__title

    &__title {
        margin-bottom: 48px;
        @media (max-width: $md-4) {
            margin-bottom: 38px;
         }
    }

    // .steps__content

    &__content {
    }

    // .steps__item

    &__item {
        width: 100%;
        display: flex;
        align-items: center;
        margin-bottom: 24px;
    }

    // .steps__number

    &__number {
        min-width: 68px;
        min-height: 68px;
        background-color: $greenColor;
        display: flex;
        align-items: center;
        justify-content: center;
        color: #fff;
        font-family: "Exo", serif;
        font-size: 38px;
        font-weight: 600;
        border-radius: 50%;
        margin-right: 30px;
        @media (max-width: $md-4) {
            margin-right: 15px;
            min-width: 48px;
            min-height: 48px;
            font-size: 28px;
         }
    }

    // .steps__main

    &__main {
        width: 95%;
        min-height: 245px;
        background: #FFFFFF;
        box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.12);
        padding: 32px;
        display: flex;
        align-items: center;
        border-radius: 10px;
        position: relative;
        @media (max-width: $md-3) {
            padding: 22px;
         }
         @media (max-width: $md-4) {
            flex-direction: column;
            align-items: flex-start;
         }
    }

    // .steps__image

    &__image {
        width: 331px;
        height: 100%;
        margin-right: 56px;
        @media (max-width: $md-3) {
           margin-right: 26px;
        }
        @media (max-width: $md-4) {
            width: 100%;
            margin-right: 0;
            margin-bottom: 20px;
         }
    }

    // .steps__info

    &__info {
        display: flex;
        flex-direction: column;
    }

    // .steps__caption

    &__caption {
        margin-bottom: 48px;
        @media (max-width: $md-3) {
            margin-bottom: 28px;
        }
        &::before {
            width: 86px;
            bottom: -24px;
            @media (max-width: $md-3) {
                bottom: -14px;
            }
        }
    }

    // .steps__text

    &__text {
        max-width: 568px;
    }
}
