.team {
    width: 100%;
    margin-bottom: 130px;
    @media (max-width: $md-4) {
            margin-bottom: 75px;
    }
    // .team__container

    &__container {
    }

    // .team__body

    &__body {
        width: 100%;
        height: 100%;
    }

    // .team__title

    &__title { 
        margin-bottom: 72px;
        @media (max-width: $md-4) {
            margin-bottom: 32px;
        }
    }

    // .team__content

    &__content {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        @media (max-width: $md-2) {
            grid-template-columns: 1fr;
        }
    }

    // .team__item

    &__item {
        min-height: 210px;
        background: #FFFFFF;
        box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.15);
        border-radius: 10px;
        padding: 32px 32px 32px 83px;
        position: relative;
        display: flex;
        align-items: center;
        margin: 0 15px 24px 15px;
        @media (max-width: $md-2) {
            margin: 0 0 24px 0;
        }
        @media (max-width: $md-5) {
            padding: 22px 22px 22px 23px;
        }
        &::before {
            position: absolute;
            background-color: $greenColor;
            content: "";
            width: 51px;
            height: 100%;
            top: 0;
            left: 0;
            border-top-left-radius: 10px;
            border-bottom-left-radius: 10px;
            @media (max-width: $md-5) {
               width: 15px;
            }
        }
    }

    // .team__photo

    &__photo {
        width: 138px;
        height: 138px;
        border-radius: 50%;
        margin-right: 40px;
        @media (max-width: $md-5) {
            margin-right: 10px;
            width: 98px;
            height: 98px;
         }
    }

    // .team__column

    &__column {
        display: flex;
        flex-direction: column;
    }

    // .team__name

    &__name {
        margin-bottom: 12px;
        @media (max-width: $md-5) {
            margin-bottom: 5px;
            font-size: 18px;
         }
    }

    // .team__position

    &__position {
        margin-bottom: 48px;
        @media (max-width: $md-5) {
            margin-bottom: 38px;
         }
        &::before {
            bottom: -16px;
            width: 86px;
        }
    }

    // .team__contacts

    &__contacts {
        display: flex;
        align-items: flex-end;
        span {
            @include bg("contain");
            margin-right: 20px;
            cursor: pointer;
        }
        
    }
}


.contacts-1 {
    width: 22px;
    height: 22px;
}
.contacts-2 {
    width: 28px;
    height: 18px;
}
.contacts-3 {
    width: 23px;
    height: 23px;
}