.poll {
    width: 100%;
    margin-bottom: 100px;
    @media (max-width: $md-4) {
        margin-bottom: 50px;
    }
    // .poll__container

    &__container {
    }

    // .poll__body

    &__body {
        width: 100%;
        height: 100%;
    }

    // .poll__header

    &__header {
        display: flex;
        flex-direction: column;
        margin-bottom: 40px;
        @media (max-width: $md-4) {
            margin-bottom: 20px;
        }
    }

    // .poll__title

    &__title {
        margin-bottom: 35px;
        @media (max-width: $md-4) {
            margin-bottom: 15px;
        }
    }

    // .poll__text

    &__text {
        max-width: 667px;
    }

    // .poll__content

    &__content {
        display: flex;
        justify-content: space-between;
        @media (max-width: $md-3) {
            flex-direction: column-reverse;
            justify-content: center;
        }
    }

    // .poll__left

    &__left {
        display: flex;
        flex-direction: column;
    }

    // .poll__item
    &__item._active {
        border: 1px solid $greenColor;
            .poll__radio span {
                visibility: visible;
            }
    }
    &__item {
        max-width: 687px;
        min-height: 176px;
        position: relative;
        background: #FFFFFF;
        box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.12);
        border-radius: 10px;
        padding: 48px 40px;
        cursor: pointer;
        display: flex;
        align-items: center;
        margin-bottom: 16px;
        @media (max-width: $md-3) {
            width: 100%;
            max-width: 100%;
        }
        @media (max-width: $md-4) {
            padding: 28px 20px;
        }
    }
    

    // .poll__image

    &__image {
        position: absolute;
        right: 0;
        bottom: 0;
        @media (max-width: $md-4) {
            width: 100px;
        }
    }

    // .poll__radio

    &__radio {
        min-width: 24px;
        min-height: 24px;
        border-radius: 50%;
        border: 1px solid #CBCBCD;
        box-shadow: inset 0px 4px 4px rgba(0, 0, 0, 0.2);
        display: flex;
        align-items: center;
        justify-content: center;
        margin-right: 34px;
        @media (max-width: $md-4) {
            margin-right: 14px;
        }
        span {
            border-radius: 50%;
            background-color: $greenColor;
            width: 10px;
            height: 10px;
            visibility: hidden;
        }
    }

    // .poll__block

    &__block {
        position: relative;
        z-index: 5;
        display: flex;
        flex-direction: column;
    }

    // .poll__caption

    &__caption {
        font-size: 20px;
        margin-bottom: 24px;
        @media (max-width: $md-4) {
            margin-bottom: 10px;
        }
    }

    // .poll__description

    &__description {
        max-width: 466px;
    }

    // .poll__right

    &__right { 
        display: flex;
        flex-direction: column;
        font-size: 20px;
        @media (max-width: $md-3) {
            margin-bottom: 40px;
        }
    }

    // .poll__info

    &__info {
        font-family: "Exo", sans-serif;
        margin-bottom: 25px;
        @media (max-width: $md-3) {
            margin-bottom: 15px;
        }
    }

    // .poll__users

    &__users {
        font-family: "Exo", sans-serif;
        font-weight: 600;
        width: 280px;
        &::before {
            bottom: -39px;
            @media (max-width: $md-3) {
                bottom: -10px;
            }
        }
        span {
            font-size: 54px;
            margin-right: 10px;
        }
    }
}
