.companies {
    width: 100%;
    margin-bottom: 130px;
    @media (max-width: $md-4) {
        margin-bottom: 75px;
    }
    // .companies__container

    &__container {
    }

    // .companies__body

    &__body {
        width: 100%;
        height: 100%;
    }

    // .companies__title

    &__title {
        margin-bottom: 72px;
        @media (max-width: $md-4) {
            margin-bottom: 32px;
        }
    }

    // .companies__content

    &__content {
        width: 100%;
    }

    // .companies__row

    &__row {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        min-height: 183px;
       @media (max-width: $md-3) {
        grid-template-columns: repeat(2, 1fr);
       }
       @media (max-width: $md-5) {
        grid-template-columns: 1fr;
    }
    }

    // .companies__row-first

    &__row-first {
        @media (max-width: $md-3) {
            margin-bottom: 30px;
        }
        margin-bottom: 71px;
    }

    // .companies__item

    &__item {
        &:last-child {
            border: none;
            @media (max-width: $md-3) {
                border-bottom: 1px solid $lightGreyColor;
            }
        }
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;
        height: 100%;
        text-align: center;
        border-right: 1px solid $lightGreyColor;
        @media (max-width: $md-3) {
           border: none;
           border-bottom: 1px solid $lightGreyColor;
            padding: 30px 0;
            margin: 0 15px;
        }
    }

    // .companies__logo

    &__logo {
        @media (max-width: $md-3) {
            margin-bottom: 20px;
         }
    }

    // .companies__name

    &__name {
        margin-bottom: 14px;
        @media (max-width: $md-5) {
            grid-template-columns: 1fr;
        }
    }

    // .companies__description

    &__description {

    }
}
