.first-screen {
    width: 100%;
    position: relative;
    min-height: 100vh;
    display: flex;
    align-items: center;
    // .first-screen__bg

    &__bg {
        position: absolute;
        top: 0;
        right: 0;
        height: 100%;
        width: 40%;
        background-color: $greenColor;
        z-index: -50;
        @media (max-width: $md-3) {
           display: none;
        }
    }

    // .first-screen__container

    &__container {
        height: 100%;
       
    }

    // .first-screen__body

    &__body {
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
        @media (max-width: $md-3) {
            flex-direction: column;
            justify-content: center;
            align-items: flex-start;
        }
    }

    // .first-screen__content

    &__content {
        margin-right: 30px;
        padding: 10px 0;
        @media (max-width: $md-3) {
            margin-right: 0;
            margin-bottom: 50px;
        }
    }

    // .first-screen__title

    &__title {
        max-width: 610px;
        margin-bottom: 40px;
        @media (max-width: $md-4) {
            margin-bottom: 20px;
        }
    }

    // .first-screen__text

    &__text {
        max-width: 525px;
        margin-bottom: 40px;
        @media (max-width: $md-4) {
            margin-bottom: 20px;
        }
    }

    // .first-screen__more

    &__more {
        font-family: "PT-Sans", sans-serif;
        font-weight: 700;
        width: 105px;
        cursor: pointer;
        &::before {
            bottom: -10px;
        }
        &::after {
            position: absolute;
            display: block;
            content: "";
            background-color: $darkColor; 
            height: 3px;
            width: 0;
            left: 0;
            bottom: -10px; 
            transition: .3s all linear 0s;
        }
        &:hover {
            &::after {
                width: 100%;
            }
        }
    }

    // .first-screen__image

    &__image {
        width: auto;
        object-fit: contain;
        width: 50%;
        @media (max-width: $md-3) {
            width: 100%;
            
            border-top-right-radius: 14px;
         }
    }
}
