.works {
    width: 100%;
    margin-bottom: 200px;
    @media (max-width: $md-4) {
        margin-bottom: 70px;
    }
    // .works__container

    &__container {
    }

    // .works__body

    &__body {
        width: 100%;
        height: 100%;
    }

    // .works__header

    &__header {
        width: 100%;
        margin-bottom: 56px;
        @media (max-width: $md-4) {
            margin-bottom: 26px;
        }
    }

    // .works__title

    &__title {
        margin-bottom: 32px;
        @media (max-width: $md-4) {
            margin-bottom: 20px;
        }
    }

    // .works__text

    &__text {
        max-width: 670px;
    }

    // .works__content

    &__content {
    }

    // .works__slider

    &__slider {
    }

    // .works__item

    &__item {
        margin: 15px;
    }
}

.works-item {
    height: 440px;
    background: #FFFFFF;
    box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.12);
    border-radius: 10px;
    @media (max-width: $md-6) {
        height: 400px;
    }
    // .works-item__image

    &__image {
        height: 247px;
        width: 100%;
        margin-bottom: 24px;
        @media (max-width: $md-6) {
            height: 170px;
        }
    }

    // .works-item__content

    &__content {
        padding: 0 34px 34px 34px;
        @media (max-width: $md-6) {
            padding: 0 14px 14px 14px;
        }
    }

    // .works-item__title

    &__title {
        margin-bottom: 16px;
    }

    // .works-item__text

    &__text {
        max-width: 290px;
        margin-bottom: 32px;
    }

    // .works-item__price

    &__price {
        font-family: "Exo", sans-serif;
        width: 92px;
        &::before {
            bottom: -5px;
        }
    }
}

.works-slider {
    width: 100%;
    position: relative;
}
.works-slider .slick-dots{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 56px;
    padding: 0;
    @media (max-width: $md-4) {
        margin-top: 16px;
    }
}
.works-slider .slick-dots li{
    list-style: none;
    margin: 0 17px;
    min-width: 36px;
    min-height: 36px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    cursor: pointer;
    @media (max-width: $md-4) {
        min-width: 30px;
        min-height: 30px;
        margin: 0 5px;
        
    }
    @media (max-width: $md-6) {
        min-width:auto;
        min-height: auto;
        margin: 4px;
        border: none;
    }
}
.works-slider .slick-dots li.slick-active{
   border: 1px solid $greenColor;
   @media (max-width: $md-6) {  
    border: none;
    }
}
.works-slider .slick-dots li button{
    font-size: 0;
    width: 18px;
    height: 18px;
    background-color: $lightGreyColor;
    border-radius: 50%;
    border: 1px solid transparent;
    cursor: pointer;
    @media (max-width: $md-6) {
        max-width: 14px;
        max-height: 14px;
        
    }
}
.works-slider .slick-dots li.slick-active button {
    background-color: $greenColor;
}

