.clients {
    width: 100%;
    margin-bottom: 120px;
    @media (max-width: $md-4) {
        margin-bottom: 50px;
    }
    // .clients__container

    &__container {
    }

    // .clients__body

    &__body {
        width: 100%;
        height: 100%;
        position: relative;
        min-height: 910px;
        background-color: $greenColor;
        padding: 85px 67px 85px 81px;
        display: flex;
        justify-content: space-between;
        @media (max-width: $md-1) {
            flex-direction: column;
            justify-content: center;
            padding: 85px 30px 85px 30px;
        }
        @media (max-width: $md-4) {
            padding: 40px 15px 45px 15px;
        }
    }

    // .clients__illustration

    &__illustration {
        position: absolute;
        object-fit: contain;
        top: 0;
        left: 0;
        margin-left: 33px;
        @media (max-width: $md-4) {
            display: none;
        }
    }

    // .clients__title

    &__title {
        max-width: 447px;
        color: #fff;
        z-index: 5;
        margin-bottom: 40px;
        @media (max-width: $md-5) {
            margin-bottom: 20px;
            font-size: 35px;
         }
    }

    // .clients__paper

    &__paper {
       z-index: 5;
    }
}

.clients-paper {
    background: #FFFFFF;
    border-radius: 8px;
    padding: 0 32px 40px 32px;
    width: 650px;
    height: 100%;
    @media (max-width: $md-4) {
        width: 100%;
        padding: 0 12px 20px 12px;
    }
    // .clients-paper__header

    &__header {
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-top: 28px;
        padding-bottom: 32px;
        margin-bottom: 32px;
        border-bottom: 1px solid $lightGreyColor;
        @media (max-width: $md-4) {
            margin-bottom: 12px;
            padding-bottom: 12px;
        }
        @media (max-width: $md-5) {
           flex-direction: column;
            align-items: flex-start;
        }
    }

    // .clients-paper__logo

    &__logo {
        @include bg("contain");
        width: 100px;
        height: 57px;
        @media (max-width: $md-5) {
            margin-bottom: 10px;
         }
    }

    // .clients-paper__info

    &__info {
        font-size: 11px;
        max-width: 274px;
    }

    // .clients-paper__chart

    &__chart {
        width: 100%;
        height: 100%;
    }

    // .clients-paper__top

    &__top {
        width: 100%;
        height: 45px;
        background: #0B7572;
        border-radius: 2px;
        display: flex;
        align-items: center;
        padding: 4px 0;
        margin-bottom: 8px;
        @media (max-width: $md-4) {
           display: none;
         }
    }

    // .clients-paper__caption

    &__caption {
        font-size: 16px;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        color: #fff;
        font-family: "Exo", sans-serif;
        border-right: 1px solid rgba(0, 0, 0, 0.2);
    }

    // .clients-paper__caption-last

    &__caption-last {
        border: none;
    }

    // .clients-paper__content

    &__content {
    }

    // .clients-paper__row

    &__row {
        display: flex;
        align-items: center;
        border-bottom: 1px solid $lightGreyColor;
        height: 155px;
        @media (max-width: $md-4) {
            flex-direction: column;
            height: auto;
            align-items: flex-start;
            padding-bottom: 20px;
        }
    }

    // .clients-paper__item

    &__item {
        display: flex;
        flex-direction: column;
        padding-bottom: 24px;
        font-size: 14px;
        border-right: 1px solid $lightGreyColor;
        height: 100%;
        padding-left: 16px;
        @media (max-width: $md-4) {
           border: none;
           padding-left: 0;
           width: 100%;
           padding-bottom: 0;
        }
    }

    // .clients-paper__item-first

    &__item-first {
        padding-left: 0;
    }

    // .clients-paper__image

    &__image {
        width: 81px;
        height: 74px;
        padding-top: 14px;
        @media (max-width: $md-4) {
           height: 150px;
           width: 200px;
           margin-bottom: 10px;
         }
    }

    // .clients-paper__text

    &__text {
        margin-bottom: 10px;
        max-width: 112px;
        padding-top: 14px;
        @media (max-width: $md-4) {
            max-width: 100%;
            width: 100%;
            padding-top: 0;
            margin-bottom: 5px;
         }
    }

    // .clients-paper__price

    &__price {
        font-family: "Exo", sans-serif;
        font-weight: 600;
        margin-bottom: 10px;
    }

    // .clients-paper__manufacturer

    &__manufacturer {
        font-family: "PT-Sans", sans-serif;
        font-weight: 700;
        margin-bottom: 10px;
        padding-top: 24px;
        padding-top: 14px;
        @media (max-width: $md-4) {
            max-width: 100%;
            width: 100%;
            padding-top: 0;
            margin-bottom: 5px;
         }
    }

    // .clients-paper__address

    &__address {
        margin-bottom: 12px;
        max-width: 141px;
        @media (max-width: $md-4) {
            max-width: 100%;
            width: 100%;
            padding-top: 0;
         }
    }

    // .clients-paper__rating

    &__rating {
        display: flex;
        align-items: center;
        @media (max-width: $md-4) {
            margin-bottom: 10px;
         }
        span {
            @include bg("contain");
            width: 12px;
            height: 11px;
            margin-right: 4px;
        }
    }
 
    // .clients-paper__name

    &__name {
        font-family: "PT-Sans", sans-serif;
        font-weight: 700;
        margin-bottom: 12px;
        max-width: 114px;
        padding-top: 14px;
        @media (max-width: $md-4) {
            max-width: 100%;
            width: 100%;
            padding-top: 0;
            margin-bottom: 5px;
         }
    }

    // .clients-paper__about

    &__about {
        margin-bottom: 10px;
        max-width: 114px;
        @media (max-width: $md-4) {
            max-width: 100%;
            width: 100%;
            padding-top: 0;
            margin-bottom: 5px;
         }
    }

    // .clients-paper__phone-number

    &__phone-number {
        font-family: "PT-Sans", sans-serif;
    }
}

.column-photo {
    width: 112px;
}
.column-name {
    width: 147px;
}
.column-manufacturer {
    width: 175px;
}
.column-contact {
    width: 154px;
}
.column-photo, 
.column-name,
.column-manufacturer,
.column-contact {
    @media (max-width: $md-4) {
        width: 100%;
    }
   
}