.contacts {
    width: 100%;
    margin-bottom: 140px;
    @media (max-width: $md-4) {
        margin-bottom: 30px;
    }
    // .contacts__container

    &__container {
    }

    // .contacts__body

    &__body {
        width: 100%;
        height: 100%;
    }

    // .contacts__title

    &__title {
        margin-bottom: 94px;
        @media (max-width: $md-4) {
            margin-bottom: 44px;
        }
    }

    // .contacts__content

    &__content {
        display: flex;
        justify-content: space-between;
        @media (max-width: $md-4) {
            flex-direction: column;
            justify-content: center;
        }
    }

    // .contacts__left

    &__left {
        @media (max-width: $md-4) {
            margin-bottom: 20px;
        }
    }

    // .contacts__item

    &__item {
        display: flex;
        align-items: center;
        margin-bottom: 50px;
        @media (max-width: $md-4) {
            margin-bottom: 30px;
        }
    }

    // .contacts__logo

    &__logo {
        @include bg("contain");
        width: 24px;
        height: 24px;
        margin-right: 36px;
    }

    // .contacts__text

    &__text {

    }

    // .contacts__caption

    &__caption {
        margin-bottom: 8px;
    }

    // .contacts__main

    &__main {
        font-family: "Exo", sans-serif;
        font-weight: 600;
        text-decoration: none;
        color: $darkColor;
    }

    // .contacts__right

    &__right {
        
    }

    // .contacts__row

    &__row {
    }
}

.contacts-row {
    width: 674px;
    margin-bottom: 32px;
    padding-bottom: 16px;
    border-bottom: 1px solid $lightGreyColor;
    display: flex;
    align-items: center;
    justify-content: space-between;
    @media (max-width: $md-3) {
        width: 400px;
    }
    @media (max-width: $md-4) {
        width: 100%;
    }
    // .contacts-row__left

    &__left {
        width: 40%;
        @media (max-width: $md-3) {
            width: 45%;
        }
        @media (max-width: $md-5) {
            width: 25%;
        }
    }

    // .contacts-row__content

    &__content {
        font-family: "Exo", sans-serif;
        font-weight: 600;
        width: 60%;
        @media (max-width: $md-3) {
            width: 55%;
        }
    }
}
