.pluses {
    width: 100%;
    margin-bottom: 100px;
    @media (max-width: $md-4) {
        margin-bottom: 50px;
    }
    // .pluses__top

    &__top {
        width: 100%;
        min-height: 450px;
        position: relative;
        padding: 160px 0;
        @media (max-width: $md-5) {
            min-height: 350px;
            padding: 140px 0;
        }
    }

    // .pluses__container

		&__container {
            height: 100%;
		}

    // .pluses__bg

    &__bg {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
        z-index: -50;
    }

    // .pluses__line

    &__line {
        position: absolute;
        z-index: -49;
        height: 100%;
        top: 0;
        left: 30%;
        display: none;
    }

    // .pluses__green

    &__green {
        position: absolute;
        z-index: -48;
        height: 100%;
        top: 0;
        left: 0;
        width: 55%;
        @media (max-width: $md-1) {
            
        }
        @media (max-width: $md-2) {
            width: 70%;
        }
        @media (max-width: $md-4) {
            display: none;
        }
    }

    // .pluses__title

    &__title {
        color: #fff;
        max-width: 360px;
    }

    // .pluses__content

    &__content {
        padding-top: 70px;
        width: 100%;
        height: 100%;
        @media (max-width: $md-4) {
            padding-top: 40px;
        }
    }

    // .pluses__body

    &__body {
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
        @media (max-width: $md-3) {
            flex-direction: column;
            justify-content: center;
        }
    }

    // .pluses__block

    &__block {
        display: flex;
        flex-direction: column;

    }

    // .pluses__item

    &__item {
        display: flex;
        align-items: center;
        margin-bottom: 64px;
        @media (max-width: $md-3) {
            margin-bottom: 44px;
        }
    }

    // .pluses__ico

    &__ico {
        min-width: 80px;
        min-height: 80px;
        background-color: $lightGreyColor;
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        margin-right: 40px;
        @media (max-width: $md-3) {
            margin-right: 20px;
            min-width: 60px;
            min-height: 60px;
        }
        span {
            @include bg("contain");
            width: 45px;
            height: 45px;
            @media (max-width: $md-3) {
                width: 30px;
                height: 30px;
            }
        }
    }

    // .pluses__description

    &__description {
       
    }

    // .pluses__caption

    &__caption {
        margin-bottom: 16px;
        @media (max-width: $md-5) {
            margin-bottom: 5px;
            font-size: 18px;
        }
    }

    // .pluses__text

    &__text {
        max-width: 290px;
    }
}
