

.services {
    width: 100%;
    position: relative;
    margin-bottom: 100px;
    @media (max-width: $md-4) {
        margin-bottom: 50px;
    }
    // .services__container

    &__container {
    }

    // .services__body

    &__body {
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
    }

    // .services__row

    &__row {
        position: relative;
        top: -100px;
        width: 100%;
        min-height: 341px;
        background-color: #fff;
        box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.15);
        border-radius: 20px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 40px 80px;
        @media (max-width: $md-2) {
           padding: 20px 40px;
        }
        @media (max-width: $md-3) {
            padding: 40px 20px;
            top: 0;
            margin-top: 50px;
            flex-direction: column;
            justify-content: center;
            width: 90%;
            text-align: center;
         }
         @media (max-width: $md-4) {
           margin-top: 30px;
        }
    }

    // .services__item

    &__item {
        @media (max-width: $md-3) {
            display: flex;
            flex-direction: column;
            align-items: center;
            margin-bottom: 20px;
         }
    }

    // .services__ico

    &__ico {
        width: 92px;
        height: 92px;
        background-color: #E9E9EC;
        display: flex;
        align-items: center;
        justify-content: center;
        margin-bottom: 45px;
        border-radius: 50%;
        @media (max-width: $md-3) {
            margin-bottom: 25px;
         }
        span {
            @include bg("contain");
            width: 50px;
            height: 50px;
        }
        .services-ico-2, .services-ico-3 {
            @include bg("contain");
            width: 80px;
            height: 80px;
        }
    }

    // .services__caption

    &__caption {
        max-width: 228px;
        margin-bottom: 16px;
        font-size: 20px;
        @media (max-width: $md-3) {
            max-width: 300px;
         }    
        }

    // .services__text

    &__text {
        max-width: 225px;
        @media (max-width: $md-3) {
            max-width: 300px;
         }    
    }

    // .services__item-center

    &__item-center {
        border-left: 1px solid #D7D7DD;
        border-right: 1px solid #D7D7DD;
        padding: 0 100px;
        @media (max-width: $md-1) {
            padding: 0 50px;
        }
        @media (max-width: $md-3) {
            padding: 0;
            border: none;
         }
    }
}

