*,
*::before,
*::after {
	box-sizing: border-box;
}

.flex {
	display: flex;
	align-items: flex-start;
	justify-content: flex-start;
}

.--just-space {
	justify-content: space-between;
}

.--just-center {
	justify-content: center;
}

.--just-end {
	justify-content: flex-end;
}

.--align-str {
	align-items: stretch;
}

.--align-center {
	align-items: center;
}

.--align-end {
	align-items: flex-end;
}

.--dir-col {
	flex-direction: column;
}

html {
	font-size: 16px;
}

html,
body {
	min-height: 100%;
	position: relative;
}

body {
	font-size: 1rem;
}

ul,
ol,
li,
p,
h1,
h2,
h3,
h4,
h5,
h6 {
	margin: 0;
}

// Vars
$container: 1240px + 30px;
$md-1: $container;
$md-2: 1039.98px;
$md-3: 991.98px;
$md-4: 767.98px;
$md-5: 479.98px;
$md-6: 399.98px;

$darkColor: #363738;
$greyColor: #6B6B6C;
$greenColor: #0B7572;
$lightGreyColor: #E9E9EC;
 
// Elemements
.body {
	color: $darkColor;
}

.container {
	max-width: $container;
	height: 100%;
	margin: 0 auto;
	padding: 0 15px;
}

.title {
	font-family: "Exo", sans-serif;
	font-weight: 600;
	font-size: 48px;
	color: $darkColor;
	@media (max-width: $md-4) {
		font-size: 40px;
	}
}
.title-small {
	font-family: "Exo", sans-serif;
	font-weight: 600;
	font-size: 20px;
	color: $darkColor;
}

.text {
	font-family: "PT-Sans", serif;
	color: $greyColor;
	@media (max-width: $md-4) {
		font-size: 15px;
	}
}

.text-underline {
	position: relative;
	&::before {
		position: absolute;
		display: block;
		content: "";
		background-color: #FFCE50; 
		height: 3px;
		width: 100%;
		left: 0;
		bottom: 0; 
	}
}
